"use client";

import { useState, useEffect } from "react";
import { useQuery } from "@tanstack/react-query";
import { driverLocations } from "@/lib/api";
import dynamic from "next/dynamic";
import { MapPin, RefreshCw, Users } from "lucide-react";
import type { DriverLocation } from "@/lib/types";

// Dynamically import map components to avoid SSR issues with Leaflet
const MapView = dynamic(
  () => import("@/components/maps/MapView").then((mod) => mod.MapView),
  { ssr: false }
);

const DriverMarker = dynamic(
  () =>
    import("@/components/maps/DriverMarker").then((mod) => mod.DriverMarker),
  { ssr: false }
);

const FlyToLocation = dynamic(
  () =>
    import("@/components/maps/FlyToLocation").then((mod) => mod.FlyToLocation),
  { ssr: false }
);

export default function TrackingPage() {
  const [autoRefresh, setAutoRefresh] = useState(true);
  const [mounted, setMounted] = useState(false);
  const [selectedDriverId, setSelectedDriverId] = useState<string | null>(null);
  const [flyToPosition, setFlyToPosition] = useState<[number, number] | null>(null);

  // Color palette for different drivers
  const DRIVER_COLORS = [
    "#ef4444", // red
    "#3b82f6", // blue
    "#22c55e", // green
    "#f97316", // orange
    "#8b5cf6", // purple
    "#ec4899", // pink
    "#14b8a6", // teal
    "#eab308", // yellow
  ];

  const getDriverColor = (index: number) => DRIVER_COLORS[index % DRIVER_COLORS.length];

  // Fix hydration mismatch by only rendering time on client
  useEffect(() => {
    setMounted(true);
  }, []);

  // Fetch driver locations
  const {
    data: locationsData,
    isLoading,
    refetch,
    dataUpdatedAt,
  } = useQuery({
    queryKey: ["driver-locations"],
    queryFn: () => driverLocations.getDriverLocations(),
    staleTime: 0,
    refetchInterval: autoRefresh ? 5 * 1000 : false, // Auto-refetch every 5 seconds for real-time tracking
    refetchIntervalInBackground: true,
  });

  const locations: DriverLocation[] = locationsData?.data || [];
  const activeDrivers = locations.filter((loc) => loc.assignment_id);
  const idleDrivers = locations.filter((loc) => !loc.assignment_id);

  // Default center (Jakarta, Indonesia - PMI headquarters example)
  const defaultCenter: [number, number] = [-6.2088, 106.8456];

  // Calculate map center based on driver locations
  const mapCenter: [number, number] =
    locations.length > 0
      ? [
        locations.reduce((sum, loc) => sum + loc.latitude, 0) /
        locations.length,
        locations.reduce((sum, loc) => sum + loc.longitude, 0) /
        locations.length,
      ]
      : defaultCenter;

  const handleManualRefresh = () => {
    refetch();
  };

  const lastRefresh = new Date(dataUpdatedAt);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <MapPin className="w-8 h-8 text-red-600" />
          <div>
            <h1 className="text-3xl font-bold text-gray-900">
              Real-Time Tracking
            </h1>
            <p className="text-gray-600 mt-1">
              Monitor driver locations in real-time
            </p>
          </div>
        </div>
        <div className="flex items-center gap-3">
          <button
            onClick={handleManualRefresh}
            className="flex items-center gap-2 px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
          >
            <RefreshCw className="w-4 h-4" />
            Refresh
          </button>
          <label className="flex items-center gap-2 px-4 py-2 border border-gray-300 rounded-lg cursor-pointer hover:bg-gray-50 transition-colors">
            <input
              type="checkbox"
              checked={autoRefresh}
              onChange={(e) => setAutoRefresh(e.target.checked)}
              className="rounded border-gray-300 text-red-600 focus:ring-red-500"
            />
            <span className="text-sm font-medium text-gray-700">
              Auto-refresh (30s)
            </span>
          </label>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Total Drivers</p>
              <p className="text-3xl font-bold text-gray-900 mt-1">
                {locations.length}
              </p>
            </div>
            <Users className="w-12 h-12 text-gray-400" />
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">On Duty</p>
              <p className="text-3xl font-bold text-red-600 mt-1">
                {activeDrivers.length}
              </p>
            </div>
            <div className="w-12 h-12 bg-red-100 rounded-full flex items-center justify-center">
              <MapPin className="w-6 h-6 text-red-600" />
            </div>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Idle</p>
              <p className="text-3xl font-bold text-gray-600 mt-1">
                {idleDrivers.length}
              </p>
            </div>
            <div className="w-12 h-12 bg-gray-100 rounded-full flex items-center justify-center">
              <MapPin className="w-6 h-6 text-gray-600" />
            </div>
          </div>
        </div>
      </div>

      {/* Map */}
      <div className="bg-white rounded-lg shadow overflow-hidden">
        <div className="p-4 border-b border-gray-200 flex items-center justify-between">
          <h2 className="text-lg font-semibold text-gray-900">
            Driver Locations
          </h2>
          <div className="text-sm text-gray-500">
            Last updated: {mounted ? lastRefresh.toLocaleTimeString("id-ID", { hour: "2-digit", minute: "2-digit", second: "2-digit", hour12: false }) : "--:--:--"}
          </div>
        </div>

        {isLoading ? (
          <div className="h-[600px] flex items-center justify-center bg-gray-50">
            <div className="text-center">
              <RefreshCw className="w-8 h-8 text-gray-400 animate-spin mx-auto mb-2" />
              <p className="text-gray-600">Loading map...</p>
            </div>
          </div>
        ) : locations.length === 0 ? (
          <div className="h-[600px] flex items-center justify-center bg-gray-50">
            <div className="text-center">
              <MapPin className="w-12 h-12 text-gray-400 mx-auto mb-2" />
              <p className="text-gray-600">No driver locations available</p>
              <p className="text-sm text-gray-500 mt-1">
                Drivers will appear here once they start sharing their location
              </p>
            </div>
          </div>
        ) : (
          <div className="h-[600px]">
            <MapView center={mapCenter} zoom={12}>
              <FlyToLocation position={flyToPosition} zoom={16} />
              {locations.map((location, index) => (
                <DriverMarker key={location.id} location={location} color={getDriverColor(index)} />
              ))}
            </MapView>
          </div>
        )}
      </div>

      {/* Driver List */}
      <div className="bg-white rounded-lg shadow overflow-hidden">
        <div className="p-4 border-b border-gray-200">
          <h2 className="text-lg font-semibold text-gray-900">
            Active Drivers
          </h2>
        </div>

        {locations.length === 0 ? (
          <div className="p-8 text-center text-gray-500">No drivers online</div>
        ) : (
          <div className="divide-y divide-gray-200">
            {locations.map((location, index) => (
              <div
                key={location.id}
                onClick={() => setSelectedDriverId(selectedDriverId === location.id ? null : location.id)}
                className={`p-4 cursor-pointer transition-colors ${selectedDriverId === location.id
                  ? "bg-blue-50 border-l-4 border-blue-500"
                  : "hover:bg-gray-50"
                  }`}
              >
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <div
                      className="w-4 h-4 rounded-full shadow-md border-2 border-white"
                      style={{ backgroundColor: getDriverColor(index) }}
                    />
                    <div>
                      <p className="font-medium text-gray-900">
                        {location.driver_name || "Unknown Driver"}
                      </p>
                      {location.vehicle_license_plate && (
                        <p className="text-sm text-gray-600">
                          üöó {location.vehicle_license_plate}
                        </p>
                      )}
                    </div>
                  </div>
                  <div className="text-right">
                    <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${location.assignment_id
                      ? "bg-green-100 text-green-800"
                      : "bg-gray-100 text-gray-600"
                      }`}>
                      {location.assignment_id ? "üî¥ On Duty" : "‚ö™ Idle"}
                    </span>
                    <p className="text-xs text-gray-500 mt-1">
                      {mounted && location.timestamp
                        ? new Date(location.timestamp).toLocaleTimeString("id-ID", {
                          hour: "2-digit", minute: "2-digit", second: "2-digit", hour12: false
                        })
                        : "--:--:--"}
                    </p>
                  </div>
                </div>

                {/* Detail Panel - shown when selected */}
                {selectedDriverId === location.id && (
                  <div className="mt-4 pt-4 border-t border-gray-200 animate-fadeIn">
                    <div className="grid grid-cols-2 gap-4 text-sm">
                      <div>
                        <p className="text-gray-500">Latitude</p>
                        <p className="font-mono font-medium">{location.latitude.toFixed(6)}</p>
                      </div>
                      <div>
                        <p className="text-gray-500">Longitude</p>
                        <p className="font-mono font-medium">{location.longitude.toFixed(6)}</p>
                      </div>
                      <div>
                        <p className="text-gray-500">Driver ID</p>
                        <p className="font-mono text-xs truncate">{location.driver_id}</p>
                      </div>
                      {location.assignment_id && (
                        <div>
                          <p className="text-gray-500">Assignment ID</p>
                          <p className="font-mono text-xs truncate">{location.assignment_id}</p>
                        </div>
                      )}
                    </div>
                    <a
                      href={`https://www.google.com/maps?q=${location.latitude},${location.longitude}`}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="mt-3 inline-flex items-center gap-1 text-blue-600 hover:text-blue-800 text-sm"
                    >
                      üìç Open in Google Maps
                    </a>
                  </div>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
